.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  width: 200px;
  padding: 32px 16px 16px;
}

.card__img {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__info {
  color: #060b35;
  padding-bottom: 4px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__productCode {
  color: #616070;
  padding-bottom: 16px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__buyButton {
  cursor: pointer;
  background-image: url("button.19c835d0.png");
  border: none;
  width: 100%;
  height: 40px;
}

.card__buyButton:hover {
  background-image: url("hover_button.a8b79675.png");
}

.card__price {
  justify-content: space-between;
  align-items: center;
  padding-bottom: 16px;
  display: flex;
}

.card__price_label {
  color: #616070;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price_amount {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.stars {
  justify-content: space-between;
  padding-bottom: 24px;
  display: flex;
}

.stars--4 {
  align-items: center;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.stars__text {
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

body, p {
  margin: 0;
}
